import { Heading, Slide, Text } from "spectacle";


export default (
<Slide transition={['zoom']} bgColor="primary">
<Heading size={1} fit caps lineHeight={1} textColor="secondary" textFont="primary">
  The Learning Network
</Heading>
<Text margin="10px 0 0" textColor="tertiary" size={1} bold textFont="secondary">
  product development
</Text>
</Slide>
);


